    title: 'åŒ—äº¬æ—¶é—´å€’è®¡æ—¶ - å…è´¹åœ¨çº¿ç²¾å‡†æ—¶é—´æ˜¾ç¤ºã€è‡ªå®šä¹‰å€’è®¡æ—¶å™¨ | BJTæ ¡å¯¹å·¥å…·',

    meta: [
      // æè¿°ï¼šè¯¦ç»†è¯´æ˜å·¥å…·çš„ç²¾ç¡®æ€§ï¼ˆç§’çº§æ›´æ–°ï¼‰ã€åŠŸèƒ½ï¼ˆå€’è®¡æ—¶/æ­£è®¡æ—¶ï¼‰å’Œå®ç”¨åœºæ™¯ï¼ˆæ ¡å¯¹æ—¶é—´ã€é‡è¦äº‹ä»¶æé†’ï¼‰
      {
        name: 'description',
        content: 'æä¾›ç²¾å‡†çš„åŒ—äº¬æ—¶é—´ï¼ˆBJTï¼‰æ˜¾ç¤ºï¼Œç§’çº§æ›´æ–°ï¼Œå¯ç”¨äºæ—¶é—´æ ¡å¯¹ã€‚åŒæ—¶æä¾›å¤šåŠŸèƒ½å€’è®¡æ—¶å™¨å’Œæ­£è®¡æ—¶ï¼ˆè®¡æ—¶å™¨ï¼‰åŠŸèƒ½ï¼Œå¸®åŠ©æ‚¨ç®¡ç†é‡è¦ä¼šè®®ã€è€ƒè¯•æˆ–æ´»åŠ¨æ—¶é—´ã€‚'
      },

      // å…³é”®è¯ï¼šè¦†ç›–æ—¶é—´æ ¡å¯¹ã€æ—¶åŒºã€å€’è®¡æ—¶ã€è®¡æ—¶å™¨ç­‰æ ¸å¿ƒæœç´¢è¯
      {
        name: 'keywords',
        content: 'åŒ—äº¬æ—¶é—´, å€’è®¡æ—¶å™¨, BJTæ—¶é—´æ ¡å¯¹, åœ¨çº¿å€’è®¡æ—¶, ç²¾å‡†æ—¶é—´æ˜¾ç¤º, è®¡æ—¶å™¨, ä¸­å›½æ ‡å‡†æ—¶é—´, åœ¨çº¿æ—¶é’Ÿ'
      }
    ]
  },
  data() {
    return {
      // åŒ—äº¬æ—¶é—´
      currentTime: '',
      currentDate: '',
      currentWeek: '',
      timeTimer: null,
      
      // å€’è®¡æ—¶æ¨¡å¼
      countdownMode: 'quick',
      
      // å¿«é€Ÿå€’è®¡æ—¶
      customHours: 0,
      customMinutes: 5,
      customSeconds: 0,
      presetTimes: [
        { label: '1åˆ†é’Ÿ', minutes: 1, type: 'primary' },
        { label: '5åˆ†é’Ÿ', minutes: 5, type: 'success' },
        { label: '10åˆ†é’Ÿ', minutes: 10, type: 'info' },
        { label: '15åˆ†é’Ÿ', minutes: 15, type: 'warning' },
        { label: '30åˆ†é’Ÿ', minutes: 30, type: 'danger' },
        { label: '1å°æ—¶', hours: 1, type: 'primary' }
      ],
      
      // ç›®æ ‡æ—¶é—´å€’è®¡æ—¶
      targetDate: '',
      targetTime: '',
      countdownTitle: '',
      presetTargets: [
        { name: 'ä»Šå¤©ä¸‹ç­', time: '18:00:00' },
        { name: 'æ˜å¤©ä¸Šç­', time: '09:00:00', addDays: 1 },
        { name: 'å‘¨æœ«', time: '00:00:00', nextWeekend: true },
        { name: 'æ–°å¹´', date: '2025-01-01', time: '00:00:00' }
      ],
      
      // å€’è®¡æ—¶çŠ¶æ€
      isCountdownActive: false,
      isPaused: false,
      isFinished: false,
      countdownTimer: null,
      totalSeconds: 0,
      remainingTotalSeconds: 0,
      remainingDays: 0,
      remainingHours: 0,
      remainingMinutes: 0,
      remainingSeconds: 0,
      
      // å€’è®¡æ—¶å†å²
      countdownHistory: [],

      // é€šçŸ¥æƒé™çŠ¶æ€
      notificationPermission: 'default'
    }
  },
  computed: {
    hasCustomTime() {
      return this.customHours > 0 || this.customMinutes > 0 || this.customSeconds > 0
    },
    
    isUrgent() {
      return this.remainingTotalSeconds <= 60 && this.remainingTotalSeconds > 0
    },
    
    countdownProgress() {
      if (this.totalSeconds === 0) return 0
      return Math.round(((this.totalSeconds - this.remainingTotalSeconds) / this.totalSeconds) * 100)
    },
    
    progressColor() {
      if (this.countdownProgress < 30) return '#67c23a'
      if (this.countdownProgress < 70) return '#e6a23c'
      return '#f56c6c'
    },

    notificationStatus() {
      if (!('Notification' in window)) {
        return { text: 'ä¸æ”¯æŒ', color: '#909399', icon: 'el-icon-warning' }
      }

      switch (this.notificationPermission) {
        case 'granted':
          return { text: 'å·²å¼€å¯', color: '#67c23a', icon: 'el-icon-success' }
        case 'denied':
          return { text: 'å·²æ‹’ç»', color: '#f56c6c', icon: 'el-icon-error' }
        default:
          return { text: 'æœªè®¾ç½®', color: '#e6a23c', icon: 'el-icon-question' }
      }
    },

    canRequestNotification() {
      return 'Notification' in window && this.notificationPermission !== 'granted'
    }
  },
  mounted() {
    this.startTimeDisplay()
    this.loadCountdownHistory()
    this.updateNotificationPermission()
    this.requestNotificationPermission()
  },
  beforeDestroy() {
    this.clearTimers()
  },
  methods: {
    startTimeDisplay() {
      this.updateBeijingTime()
      this.timeTimer = setInterval(() => {
        this.updateBeijingTime()
      }, 1000)
    },
    
    updateBeijingTime() {
      const now = new Date()
      // è½¬æ¢ä¸ºåŒ—äº¬æ—¶é—´ (UTC+8)
      const beijingTime = new Date(now.getTime() + (8 * 60 * 60 * 1000) + (now.getTimezoneOffset() * 60 * 1000))
      
      this.currentTime = beijingTime.toLocaleTimeString('zh-CN', { hour12: false })
      this.currentDate = beijingTime.toLocaleDateString('zh-CN', { 
        year: 'numeric', 
        month: 'long', 
        day: 'numeric' 
      })
      this.currentWeek = beijingTime.toLocaleDateString('zh-CN', { weekday: 'long' })
    },
    
    setPresetTime(preset) {
      const totalSeconds = (preset.hours || 0) * 3600 + (preset.minutes || 0) * 60 + (preset.seconds || 0)
      this.startCountdownWithSeconds(totalSeconds, preset.label)
    },
    
    startCustomCountdown() {
      const totalSeconds = this.customHours * 3600 + this.customMinutes * 60 + this.customSeconds
      if (totalSeconds > 0) {
        const title = `${this.customHours}æ—¶${this.customMinutes}åˆ†${this.customSeconds}ç§’`
        this.startCountdownWithSeconds(totalSeconds, title)
      }
    },
    
    startTargetCountdown() {
      if (!this.targetDate || !this.targetTime) return
      
      const targetDateTime = new Date(`${this.targetDate} ${this.targetTime}`)
      const now = new Date()
      const beijingNow = new Date(now.getTime() + (8 * 60 * 60 * 1000) + (now.getTimezoneOffset() * 60 * 1000))
      
      const diffMs = targetDateTime.getTime() - beijingNow.getTime()
      
      if (diffMs <= 0) {
        this.$message.error('ç›®æ ‡æ—¶é—´å¿…é¡»æ™šäºå½“å‰æ—¶é—´')
        return
      }
      
      const totalSeconds = Math.floor(diffMs / 1000)
      const title = this.countdownTitle || `${this.targetDate} ${this.targetTime}`
      this.startCountdownWithSeconds(totalSeconds, title)
    },
    
    setPresetTarget(target) {
      const now = new Date()
      const beijingNow = new Date(now.getTime() + (8 * 60 * 60 * 1000) + (now.getTimezoneOffset() * 60 * 1000))
      
      let targetDate = new Date(beijingNow)
      
      if (target.date) {
        targetDate = new Date(target.date)
      } else if (target.addDays) {
        targetDate.setDate(targetDate.getDate() + target.addDays)
      } else if (target.nextWeekend) {
        const daysUntilSaturday = (6 - targetDate.getDay()) % 7
        targetDate.setDate(targetDate.getDate() + (daysUntilSaturday || 7))
      }
      
      this.targetDate = targetDate.toISOString().split('T')[0]
      this.targetTime = target.time
      this.countdownTitle = target.name
    },
    
    startCountdownWithSeconds(totalSeconds, title) {
      this.totalSeconds = totalSeconds
      this.remainingTotalSeconds = totalSeconds
      this.countdownTitle = title
      this.isCountdownActive = true
      this.isPaused = false
      this.isFinished = false
      
      this.updateCountdownDisplay()
      this.countdownTimer = setInterval(() => {
        if (!this.isPaused) {
          this.remainingTotalSeconds--
          this.updateCountdownDisplay()
          
          if (this.remainingTotalSeconds <= 0) {
            this.finishCountdown()
          }
        }
      }, 1000)
      
      // æ·»åŠ åˆ°å†å²è®°å½•
      this.addToHistory(title, 'started')
    },
    
    updateCountdownDisplay() {
      const total = this.remainingTotalSeconds
      
      this.remainingDays = Math.floor(total / (24 * 3600))
      this.remainingHours = Math.floor((total % (24 * 3600)) / 3600)
      this.remainingMinutes = Math.floor((total % 3600) / 60)
      this.remainingSeconds = total % 60
    },
    
    pauseCountdown() {
      this.isPaused = true
      this.$message.info('å€’è®¡æ—¶å·²æš‚åœ')
    },
    
    resumeCountdown() {
      this.isPaused = false
      this.$message.success('å€’è®¡æ—¶å·²ç»§ç»­')
    },
    
    stopCountdown() {
      this.clearCountdownTimer()
      this.isCountdownActive = false
      this.addToHistory(this.countdownTitle, 'stopped')
      this.$message.warning('å€’è®¡æ—¶å·²åœæ­¢')
    },
    
    finishCountdown() {
      this.clearCountdownTimer()
      this.isFinished = true
      this.addToHistory(this.countdownTitle, 'completed')
      
      // æ’­æ”¾æç¤ºéŸ³ï¼ˆå¦‚æœæµè§ˆå™¨æ”¯æŒï¼‰
      this.playNotificationSound()
      
      // æ˜¾ç¤ºé€šçŸ¥
      this.$message({
        message: `å€’è®¡æ—¶å®Œæˆï¼š${this.countdownTitle}`,
        type: 'success',
        duration: 5000
      })
      
      // æµè§ˆå™¨é€šçŸ¥
      this.showBrowserNotification()
    },
    
    playNotificationSound() {
      try {
        const audio = new Audio('data:audio/wav;base64,UklGRnoGAABXQVZFZm10IBAAAAABAAEAQB8AAEAfAAABAAgAZGF0YQoGAACBhYqFbF1fdJivrJBhNjVgodDbq2EcBj+a2/LDciUFLIHO8tiJNwgZaLvt559NEAxQp+PwtmMcBjiR1/LMeSwFJHfH8N2QQAoUXrTp66hVFApGn+DyvmwhBSuBzvLZiTYIG2m98OScTgwOUarm7blmGgU7k9n1unEiBC13yO/eizEIHWq+8+OWT')
        audio.play().catch(() => {
          // å¿½ç•¥æ’­æ”¾å¤±è´¥
        })
      } catch (error) {
        // å¿½ç•¥é”™è¯¯
      }
    },

    updateNotificationPermission() {
      if ('Notification' in window) {
        this.notificationPermission = Notification.permission
      } else {
        this.notificationPermission = 'unsupported'
      }
    },

    requestNotificationPermission() {
      if ('Notification' in window && Notification.permission === 'default') {
        Notification.requestPermission().then(permission => {
          this.updateNotificationPermission()
          if (permission === 'granted') {
            this.$message.success('æµè§ˆå™¨é€šçŸ¥æƒé™å·²å¼€å¯ï¼Œå€’è®¡æ—¶å®Œæˆæ—¶å°†æ”¶åˆ°é€šçŸ¥')
          } else if (permission === 'denied') {
            this.$message.warning('æµè§ˆå™¨é€šçŸ¥æƒé™è¢«æ‹’ç»ï¼Œå€’è®¡æ—¶å®Œæˆæ—¶å°†åªæ˜¾ç¤ºé¡µé¢æç¤º')
          }
        })
      }
    },

    manualRequestNotification() {
      if ('Notification' in window) {
        Notification.requestPermission().then(permission => {
          this.updateNotificationPermission()
          if (permission === 'granted') {
            this.$message.success('æµè§ˆå™¨é€šçŸ¥æƒé™å·²å¼€å¯ï¼')
          } else if (permission === 'denied') {
            this.$message.error('æµè§ˆå™¨é€šçŸ¥æƒé™è¢«æ‹’ç»ï¼Œè¯·åœ¨æµè§ˆå™¨è®¾ç½®ä¸­æ‰‹åŠ¨å¼€å¯')
          }
        })
      } else {
        this.$message.error('æ‚¨çš„æµè§ˆå™¨ä¸æ”¯æŒé€šçŸ¥åŠŸèƒ½')
      }
    },

    showBrowserNotification() {
      if ('Notification' in window && Notification.permission === 'granted') {
        const notification = new Notification('ğŸš€ å€’è®¡æ—¶å®Œæˆï¼', {
          body: `${this.countdownTitle || 'å€’è®¡æ—¶ä»»åŠ¡'}å·²å®Œæˆ`,
          icon: '/favicon.ico',
          badge: '/favicon.ico',
          tag: 'countdown-finished',
          requireInteraction: true,
          silent: false
        })

        // ç‚¹å‡»é€šçŸ¥æ—¶èšç„¦åˆ°é¡µé¢
        notification.onclick = () => {
          window.focus()
          notification.close()
        }

        // 5ç§’åè‡ªåŠ¨å…³é—­é€šçŸ¥
        setTimeout(() => {
          notification.close()
        }, 5000)
      } else if ('Notification' in window && Notification.permission !== 'denied') {
        Notification.requestPermission().then(permission => {
          if (permission === 'granted') {
            const notification = new Notification('ğŸš€ å€’è®¡æ—¶å®Œæˆï¼', {
              body: `${this.countdownTitle || 'å€’è®¡æ—¶ä»»åŠ¡'}å·²å®Œæˆ`,
              icon: '/favicon.ico',
              badge: '/favicon.ico',
              tag: 'countdown-finished',
              requireInteraction: true,
              silent: false
            })

            notification.onclick = () => {
              window.focus()
              notification.close()
            }

            setTimeout(() => {
              notification.close()
            }, 5000)
          }
        })
      }
    },
    
    resetCountdown() {
      this.clearCountdownTimer()
      this.isCountdownActive = false
      this.isPaused = false
      this.isFinished = false
    },
    
    clearCountdownTimer() {
      if (this.countdownTimer) {
        clearInterval(this.countdownTimer)
        this.countdownTimer = null
      }
    },
    
    clearTimers() {
      if (this.timeTimer) {
        clearInterval(this.timeTimer)
        this.timeTimer = null
      }
      this.clearCountdownTimer()
    },
    
    addToHistory(title, status) {
      const record = {
        title: title,
        targetTime: this.targetDate && this.targetTime ? `${this.targetDate} ${this.targetTime}` : `${Math.floor(this.totalSeconds / 3600)}:${Math.floor((this.totalSeconds % 3600) / 60)}:${this.totalSeconds % 60}`,
        status: status,
        createTime: new Date().toLocaleString('zh-CN'),
        totalSeconds: this.totalSeconds,
        targetDate: this.targetDate,
        originalTargetTime: this.targetTime
      }
      
      this.countdownHistory.unshift(record)
      
      // é™åˆ¶å†å²è®°å½•æ•°é‡
      if (this.countdownHistory.length > 10) {
        this.countdownHistory = this.countdownHistory.slice(0, 10)
      }
      
      this.saveCountdownHistory()
    },
    
    repeatCountdown(record) {
      if (record.targetDate && record.originalTargetTime) {
        this.countdownMode = 'target'
        this.targetDate = record.targetDate
        this.targetTime = record.originalTargetTime
        this.countdownTitle = record.title
        this.startTargetCountdown()
      } else {
        this.startCountdownWithSeconds(record.totalSeconds, record.title)
      }
    },
    
    deleteHistory(index) {
      this.countdownHistory.splice(index, 1)
      this.saveCountdownHistory()
    },
    
    saveCountdownHistory() {
      localStorage.setItem('countdownHistory', JSON.stringify(this.countdownHistory))
    },
    
    loadCountdownHistory() {
      const saved = localStorage.getItem('countdownHistory')
      if (saved) {
        this.countdownHistory = JSON.parse(saved)
      }
    }
  }
}
</script>

